import { createWebHistory, createRouter } from "vue-router";
import Home from './components/Home.vue';
import List from './components/List.vue';//컴포넌트 import
import Detail from './components/Detail.vue';
import Author from './components/Author.vue';
import Comment from './components/Comment.vue';

const routes = [
  {
    path: "/",
    component : Home
  },
  {
    path: "/list", //이 경로로 접속을 하면
    component: List //이 컴포넌트를 보여줌.
  },
  {
    path: "/detail/:id(\\d+)",
    //detail/X로 접속하면 X번 게시물 보여주기(url파라미터) 
    //(\\d) - 정규식(숫자만 사용가능하도록.)
    //*반복
    component: Detail,
    children : [ //route안의 route - /detail/:id/author
      {
        path : "author", //nested routes
        component : Author,
      },
      {
        path : "comment",
        component : Comment,
      }
    ]
  },
  // {
  //   path: '/hello',
  //   component: HelloWorld,
  //   beforeEnter: (to, from)=>{ //해당 경로로 들어가기 전 함수를 실행함
  //     if(loginCheck == false){
  //       return '/login'
  //     }
  //     to, from -> $route변수랑 같이 사용가능
  //     to.fullPath -> 전체경로 / to.params.id -> id파라미터
  //   }
  // }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 